import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { RutaService } from '../ruta-service/ruta.service';

@Injectable()
export class CartService {
  cartObj: any = {};
  products: any;

  constructor(private http: HttpClient,
  		private router: Router,
      	private rutaService: RutaService) {

  	var carrito = localStorage.getItem('luvas_cart');
    var user = localStorage.getItem('luvas_user');

	  let item = JSON.parse(carrito);
    let user_id = JSON.parse(user);
    var aux_id = null;
    if (user_id) {
    	aux_id = user_id.id;
    }

    this.cartObj.cart = [];
    this.cartObj.subtotal = 0;
    this.cartObj.cantidad = 0;
    this.cartObj.id = null;
    this.cartObj.country = '';
    this.cartObj.state = '';
    this.cartObj.postcode = '';
    this.cartObj.total = 0;

    /*if (item) {
      this.cartObj.cart = item.cart;
      this.cartObj.subtotal = item.subtotal;
      this.cartObj.cantidad = item.cantidad;
      this.cartObj.id = aux_id;
      this.cartObj.country = item.country;
      this.cartObj.state = item.state;
      this.cartObj.postcode = item.postcode;
      this.cartObj.total = item.total;
    } else {
      this.cartObj.cart = [];
      this.cartObj.subtotal = 0;
      this.cartObj.cantidad = 0;
      this.cartObj.id = null;
      this.cartObj.country = '';
      this.cartObj.state = '';
      this.cartObj.postcode = '';
      this.cartObj.total = 0;
    }*/
    if (item) {
      var datos= {
        token: localStorage.getItem('luvas_token'),
        cart: JSON.stringify(item.cart)
      }
      this.http.post(this.rutaService.getRutaApi()+'productos/verificar/pedido', datos)
      .toPromise()
      .then(
      data => { 
        this.products = data;
        this.consultProducts(this.products);  
      },
      msg => {
        this.cartObj.cart = [];
      	this.cartObj.subtotal = 0;
      	this.cartObj.cantidad = 0;
      	this.cartObj.id = aux_id;
      	this.cartObj.country = '';
      	this.cartObj.state = '';
      	this.cartObj.postcode = '';
      	this.cartObj.total = 0;
      });
    }else {
      this.cartObj.cart = [];
      this.cartObj.subtotal = 0;
      this.cartObj.cantidad = 0;
      this.cartObj.id = null;
      this.cartObj.country = '';
      this.cartObj.state = '';
      this.cartObj.postcode = '';
      this.cartObj.total = 0;
    } 

   }

   consultProducts(products){
    if (this.cartObj.cart != '') {
      for (var i = 0; i < this.cartObj.cart.length; ++i) {
        let index = products.productos.findIndex((items) => items.colores[0].atributos[0].id === this.cartObj.cart[i].colores[0].atributos[0].id);
        if(index !== -1){
          products.productos[index].cantidad = this.cartObj.cart[i].cantidad;
          this.cartObj.cart[i] = products.productos[index];
          this.updateProduct(this.cartObj.cart[i], this.cartObj.cart[i].cantidad/*.toString()*/).subscribe(
            success => {
            },
            error => {
            }
          );
        } else {
          this.removeProduct(this.cartObj.cart[i]).subscribe(
            success => {
            },
            error => {
            }
          );
        }
      }
    } 
  }

  getProducts(){
    return Observable.create(observer => {

      var carrito = localStorage.getItem('luvas_cart');

      this.cartObj = JSON.parse(carrito);
      if (this.cartObj && this.cartObj.cart) {
      	var datos= {
	        token: localStorage.getItem('luvas_token'),
	        cart: JSON.stringify(this.cartObj.cart)
	      }
        this.http.post(this.rutaService.getRutaApi()+'productos/verificar/pedido', datos)
        .toPromise()
        .then(
        data => { 
          this.products = data;
          this.consultProducts(this.products);
          observer.next(this.cartObj);
          observer.complete(); 
        },
        msg => {

        	var user = localStorage.getItem('luvas_user');
    		let user_id = JSON.parse(user);

    		var aux_id = null;
		    if (user_id) {
		    	aux_id = user_id.id;
		    }

          this.cartObj = {};
          this.cartObj.cart = [];
      	  this.cartObj.subtotal = 0;
      	  this.cartObj.cantidad = 0;
      	  this.cartObj.id = aux_id;
      	  this.cartObj.country = '';
      	  this.cartObj.state = '';
      	  this.cartObj.postcode = '';
      	  this.cartObj.total = 0;
          observer.next(this.cartObj);
          observer.complete(); 
        });
      }else{
        var user = localStorage.getItem('luvas_user');
        let user_id = JSON.parse(user);

        var aux_id = null;
        if (user_id) {
          aux_id = user_id.id;
        }

          this.cartObj = {};
          this.cartObj.cart = [];
          this.cartObj.subtotal = 0;
          this.cartObj.cantidad = 0;
          this.cartObj.id = aux_id;
          this.cartObj.country = '';
          this.cartObj.state = '';
          this.cartObj.postcode = '';
          this.cartObj.total = 0;
          observer.next(this.cartObj);
          observer.complete();
      }
    });
  }

  addProduct(product, quantity) {
    return Observable.create(observer => {
      if(this.cartObj.cart.some(x => x.colores[0].atributos[0].id === product.colores[0].atributos[0].id)){
        observer.error('Este producto ya está incluido en el pedido');
        observer.complete();
      } else{
        product.cantidad = quantity;
        this.cartObj.cart.push(product);
        this.cartObj.cantidad += 1;  
        this.cartObj.subtotal += parseFloat(product.colores[0].atributos[0].precio) * quantity;
        var carrito = JSON.stringify(this.cartObj)
        localStorage.setItem('luvas_cart', carrito);
        observer.next(this.cartObj);
        observer.complete();
      }
    });
  }

  updateProduct(product, quantity) {
    return Observable.create(observer => {
      if(this.cartObj.cart.some(x => x.colores[0].atributos[0].id === product.colores[0].atributos[0].id)){
        
        if (quantity > product.colores[0].atributos[0].cantidad) {
          product.cantidad = product.colores[0].atributos[0].cantidad;
          var quantity_aux = product.cantidad;
        }else{
          product.cantidad = quantity;
          var quantity_aux = product.cantidad;
        }
        
        this.cartObj.subtotal = 0;
        this.cartObj.cart.forEach((elem) => {
          if (elem.colores[0].atributos[0].id === product.colores[0].atributos[0].id){
            this.cartObj.subtotal += parseFloat(product.colores[0].atributos[0].precio)*quantity_aux;
          } else{
            this.cartObj.subtotal += parseFloat(elem.colores[0].atributos[0].precio)*elem.cantidad;
          }
        });
        if (isNaN(this.cartObj.subtotal)) {  
          observer.error('Ingrese una cantidad válida');
          observer.complete();
        } else {
          var carrito = JSON.stringify(this.cartObj)
          localStorage.setItem('luvas_cart', carrito);
          observer.next(this.cartObj);
          observer.complete();
        }
      } else{
        observer.error('Este producto no está en el pedido');
        observer.complete();
      }
    });
  }

  getCartContents() {
    if (this.cartObj && this.cartObj.cart) {
      return this.cartObj.cart;
    }else{
      var aux = [];
      return aux;
    }
    
  }

  getCartCount(){
    if (this.cartObj && this.cartObj.cantidad) {
      return this.cartObj.cantidad;
    }else{
      return 0;
    }
    
  }
  
  getCartSubTotal(){
    if (this.cartObj && this.cartObj.subtotal) {
      return this.cartObj.subtotal.toFixed(2);
    }else{
      return 0;
    }
    
  }

  getCartId(){
    var user = localStorage.getItem('luvas_user');
    let user_id = JSON.parse(user);

    var aux_id = null;
    if (user_id) {
    	aux_id = user_id.id;
    }

    return aux_id;
  }

  getCartCountry(){
    return this.cartObj.country;
  }

  getState(){
    return this.cartObj.state;
  }

  getPostcode(){
    return this.cartObj.postcode;
  }
 
  removeProduct(product) {
    return Observable.create(observer => {
      let index = this.cartObj.cart.findIndex((item) => item.colores[0].atributos[0].id === product.colores[0].atributos[0].id);
      if(index !== -1){
        this.cartObj.cart.splice(index, 1);
        this.cartObj.cantidad -= 1;  
        this.cartObj.subtotal -= parseFloat(product.colores[0].atributos[0].precio)*product.cantidad;
        console.log(this.cartObj.subtotal);
       
        if (this.cartObj.cantidad === 0) {
          //localStorage.setItem('luvas_cart', '');
          this.deleteCar();
        } else {
          var carrito = JSON.stringify(this.cartObj)
          localStorage.setItem('luvas_cart', carrito);
        }
        observer.next(this.cartObj);
        observer.complete();
      } else{
        observer.error('Este producto no está incluido en el pedido');
        observer.complete(); 
      }
    });
  }

  setCountry(country){
    this.cartObj.country = country;
    var carrito = JSON.stringify(this.cartObj)
    localStorage.setItem('luvas_cart', carrito);
  }

  setState(state){
    this.cartObj.state = state;
    var carrito = JSON.stringify(this.cartObj)
    localStorage.setItem('luvas_cart', carrito);
  }

  setPostcode(postcode){
    this.cartObj.postcode = postcode;
    var carrito = JSON.stringify(this.cartObj)
    localStorage.setItem('luvas_cart', carrito);
  }

  setTotal(subtotal){
    this.cartObj.total = (parseFloat(subtotal)).toFixed(2);
    var carrito = JSON.stringify(this.cartObj)
    localStorage.setItem('luvas_cart', carrito);
  }



  deleteCar(){
  	this.cartObj.cart = [];
  	this.cartObj.subtotal = 0;
  	this.cartObj.cantidad = 0;
  	this.cartObj.country = '';
  	this.cartObj.state = '';
  	this.cartObj.postcode = '';
  	this.cartObj.total = 0;
  	var carrito = JSON.stringify(this.cartObj)
    localStorage.setItem('luvas_cart', carrito);

  }

}



